
/* Styles pour la navbar */
.navbar {
    display: flex;
    justify-content: space-between;
    align-items: center;
    background-color: #333; /* Fond sombre pour la navbar */
    color: white; /* Texte clair */
    padding: 0.5rem 1rem; /* Espacement interne */
}

/* Logo */
.logo {
    display: flex;
    align-items: center; /* Aligner verticalement le logo et le titre */
    gap: 10px; /* Espace entre le logo et le texte */
}

.logo-image {
    width: 50px; /* Taille du logo */
    height: auto; /* Maintenir les proportions */
}

.logo h1 {
    font-size: 1.5rem; /* Taille du titre */
    margin: 0; /* Supprimer les marges inutiles */
    color: white; /* Couleur du texte */
}

/* Menu principal */
.menu {
    list-style: none; /* Supprimer les puces */
    display: flex;
    margin: 0;
    padding: 0;
}

.menu li {
    margin-left: 1rem; /* Espace entre les éléments du menu */
}

.menu a {
    text-decoration: none; /* Supprimer le soulignement */
    color: white; /* Couleur des liens */
    font-weight: bold; /* Texte en gras */
}

/* Bouton "hamburger" */
.hamburger {
    display: none; /* Masqué par défaut */
    flex-direction: column;
    cursor: pointer;
}

.hamburger span {
    width: 25px;
    height: 3px;
    background-color: white;
    margin: 3px 0;
}

/* Responsive design */
@media (max-width: 768px) {
    .menu {
        display: none; /* Masque le menu par défaut sur mobile */
        flex-direction: column;
        position: absolute;
        top: 60px;
        right: 0;
        background-color: #333;
        width: 100%;
        padding: 1rem;
    }

    .menu.active {
        display: flex; /* Affiche le menu lorsque le bouton hamburger est cliqué */
    }

    .hamburger {
        display: flex; /* Affiche le bouton hamburger sur mobile */
    }
}