/* Styles globaux pour la page */
body {
    font-family: Arial, sans-serif; /* Définit une police par défaut */
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}

/* Centrer le contenu principal */
.container {
    max-width: 1200px; /* Largeur maximale du contenu */
    margin: 0 auto; /* Centre le contenu horizontalement */
    padding: 0 15px; /* Ajoute un peu d'espace sur les côtés */
}

/* Styles pour le titre */
h1 {
    text-align: center; /* Centre le texte */
    margin-top: 50px; /* Ajoute un espace en haut */
    color: #333; /* Couleur du texte */
}

/* Styles pour le slider */
.carousel {
    max-width: 800px; /* Limite la largeur maximale du slider */
    margin: 0 auto; /* Centre le slider */
    border-radius: 10px; /* Coins arrondis pour un look moderne */
    overflow: hidden; /* Cache les bords débordants */
    margin-top: 20px; /* Ajoute un espace au-dessus du slider */
}

.carousel img {
    width: 100%; /* Remplit toute la largeur du conteneur */
    height: 400px; /* Hauteur fixe pour toutes les images */
    object-fit: cover; /* Ajuste les images pour éviter les déformations */
}

/* Styles pour les flèches de navigation */
.carousel-control-prev,
.carousel-control-next {
    background-color: rgba(0, 0, 0, 0.5); /* Fond semi-transparent pour les flèches */
    border-radius: 50%; /* Forme circulaire pour les flèches */
    width: 40px; /* Largeur des flèches */
    height: 40px; /* Hauteur des flèches */
    display: flex;
    align-items: center;
    justify-content: center;
}

.carousel-control-prev-icon,
.carousel-control-next-icon {
    filter: brightness(10); /* Rend les icônes plus visibles */
}

/* Responsive design */
@media (max-width: 768px) {
    .carousel img {
        height: 300px; /* Réduit la hauteur des images sur mobile */
    }

    .carousel-control-prev,
    .carousel-control-next {
        width: 30px; /* Réduit la taille des flèches sur mobile */
        height: 30px;
    }
}

