/* Styles globaux */
body {
    font-family: Arial, sans-serif; /* Police par défaut */
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}

/* Styles pour la section "Découvrir notre club" */
.club-section {
    text-align: center; /* Centre le contenu */
    /* padding: 50px 20px;  */
    background-color: #f8f9fa; /* Fond clair pour la section */
}

.club-section h2 {
    font-size: 2rem; /* Taille du sous-titre */
    margin-bottom: 40px; /* Espace entre le sous-titre et le paragraphe */
    color: #333; /* Couleur du texte */
}

/* Conteneur du paragraphe */
.paragraph-container {
    position: relative; /* Permet d'utiliser les pseudo-éléments */
    display: inline-block; /* Limite la largeur au contenu */
    padding: 20px; /* Espacement intérieur */
    background: rgba(255, 255, 255, 0.8); /* Fond blanc semi-transparent */
    border-radius: 10px; /* Coins arrondis */
    box-shadow: 0 4px 6px rgba(211, 8, 8, 0.1); /* Ombre légère pour un effet 3D */
}

.paragraph-container p {
    margin: 0; /* Supprime les marges inutiles */
    font-size: 1rem; /* Taille du texte */
    color: #555; /* Couleur du texte */
}


/* Bordure inférieure gauche */
.paragraph-container .bottom-left {
    content: ''; /* Crée un pseudo-élément */
    position: absolute; /* Positionnement absolu par rapport au parent */
    bottom: 0; /* Bordure en bas */
    left: 0; /* Début à gauche */
    width: 20%; /* Longueur de la bordure (20 %) */
    height: 2px; /* Épaisseur de la bordure horizontale */
    background-color: #333; /* Couleur de la bordure */
}

/* Bordure inférieure droite */
.paragraph-container .bottom-right {
    content: ''; /* Crée un pseudo-élément */
    position: absolute; /* Positionnement absolu par rapport au parent */
    bottom: 0; /* Bordure en bas */
    right: 0; /* Début à droite */
    width: 20%; /* Longueur de la bordure (20 %) */
    height: 2px; /* Épaisseur de la bordure horizontale */
    background-color: #333; /* Couleur de la bordure */
}

/* Bordure verticale gauche */
.paragraph-container .left-border {
    content: ''; /* Crée un pseudo-élément */
    position: absolute; /* Positionnement absolu par rapport au parent */
    top: 0; /* Début en haut */
    left: 0; /* Bordure à gauche */
    width: 2px; /* Épaisseur de la bordure verticale */
    height: 20%; /* Longueur de la bordure (20 %) */
    background-color: #333; /* Couleur de la bordure */
}

/* Bordure verticale droite */
.paragraph-container .right-border {
    content: ''; /* Crée un pseudo-élément */
    position: absolute; /* Positionnement absolu par rapport au parent */
    top: 0; /* Début en haut */
    right: 0; /* Bordure à droite */
    width: 2px; /* Épaisseur de la bordure verticale */
    height: 20%; /* Longueur de la bordure (20 %) */
    background-color: #333; /* Couleur de la bordure */
}


/* Styles globaux */
body {
    font-family: Arial, sans-serif; /* Police par défaut */
    margin: 0; /* Supprime les marges par défaut */
    padding: 0; /* Supprime les paddings par défaut */
    box-sizing: border-box;
}

/* Styles pour la section "Découvrir notre club" */
.club-section {
    text-align: center; /* Centre le contenu */
    padding: 50px 20px; /* Espacement interne */
    background-color: #f8f9fa; /* Fond clair pour la section */
    margin-bottom: 0; /* Supprime la marge en bas */
}

.club-section h2 {
    font-size: 2rem; /* Taille du sous-titre */
    margin-bottom: 40px; /* Espace entre le sous-titre et le contenu */
    color: #333; /* Couleur du texte */
}

/* Conteneur des cartes */
.cards-container {
    display: flex; /* Alignement horizontal des cartes */
    justify-content: center; /* Centre les cartes horizontalement */
    gap: 20px; /* Espace entre les cartes */
    flex-wrap: wrap; /* Permet aux cartes de passer à la ligne sur les petits écrans */
}

/* Style des cartes */
.card {
    background-color: white; /* Fond blanc pour les cartes */
    border-radius: 15px; /* Coins arrondis */
    box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1); /* Ombre légère pour un effet 3D */
    overflow: hidden; /* Assure que le contenu reste dans les coins arrondis */
    width: 300px; /* Largeur fixe pour chaque carte */
    transition: transform 0.3s ease, box-shadow 0.3s ease; /* Animation au survol */
}

.card:hover {
    transform: translateY(-10px); /* Effet de levitation au survol */
    box-shadow: 0 8px 12px rgba(0, 0, 0, 0.2); /* Ombre plus prononcée au survol */
}

/* Image dans les cartes */
.card img {
    width: 100%; /* Remplit toute la largeur de la carte */
    height: auto; /* Maintient les proportions */
    object-fit: cover; /* Ajuste l'image sans déformation */
}

/* Titre et paragraphe dans les cartes */
.card h3 {
    font-size: 1.25rem; /* Taille du titre */
    margin: 15px 15px 10px; /* Espacement interne */
    color: #333; /* Couleur du texte */
}

.card p {
    font-size: 1rem; /* Taille du paragraphe */
    margin: 0 15px 15px; /* Espacement interne */
    color: #555; /* Couleur du texte */
}

/* Styles pour la section "Découvrir nos coachs" */
.coaches-section {
    text-align: center; /* Centre le contenu */
    padding: 50px 20px; /* Espacement interne */
    background-color: #f8f9fa; /* Fond clair pour la section */
    margin-top: 0; /* Supprime la marge en haut */
}

.coaches-section h2 {
    font-size: 2rem; /* Taille du titre */
    margin-bottom: 10px; /* Espace entre le titre et les étoiles */
    color: #333; /* Couleur du texte */
}

/* Étoiles dorées */
.stars {
    font-size: 1.5rem; /* Taille des étoiles */
    color: gold; /* Couleur dorée */
    margin-bottom: 40px; /* Espace entre les étoiles et les photos */
}

/* Conteneur des coachs */
.coaches-container {
    display: flex; /* Alignement horizontal des coachs */
    justify-content: center; /* Centre les coachs horizontalement */
    gap: 20px; /* Espace entre les coachs */
    flex-wrap: wrap; /* Permet aux coachs de passer à la ligne sur les petits écrans */
}

/* Style des coachs */
.coach {
    display: flex;
    flex-direction: column;
    align-items: center;
}

/* Images des coachs en forme de cercle */
.coach-image {
    width: 150px; /* Largeur fixe */
    height: 150px; /* Hauteur fixe */
    object-fit: cover; /* Ajuste l'image sans déformation */
    border-radius: 50%; /* Forme circulaire */
    border: 3px solid gold; /* Bordure dorée */
    transition: transform 0.3s ease, border-color 0.3s ease; /* Animation au survol */
}

.coach-image:hover {
    transform: scale(1.1); /* Effet d'agrandissement au survol */
    border-color: #ffcc00; /* Changement de couleur de la bordure au survol */
}

/* Styles pour la section "Commentaires" */
.commentaires-section {
    text-align: center;
    padding: 50px 20px;
    background-color: #f8f9fa;
}

.commentaires-section h2 {
    font-size: 2rem;
    margin-bottom: 40px;
    color: #333;
}

/* Formulaire pour ajouter un commentaire */
#add-comment-form {
    display: flex;
    flex-direction: column;
    align-items: center;
    margin-bottom: 40px;
}

#add-comment-form textarea {
    width: 80%;
    max-width: 600px;
    height: 100px;
    padding: 10px;
    border: 1px solid #ccc;
    border-radius: 5px;
    resize: none;
    margin-bottom: 10px;
    font-size: 1rem;
}

#add-comment-form button {
    padding: 10px 20px;
    background-color: #007bff;
    color: white;
    border: none;
    border-radius: 5px;
    cursor: pointer;
    font-size: 1rem;
}

#add-comment-form button:hover {
    background-color: #0056b3;
}

/* Style des cartes de commentaires */
.comment-card {
    background-color: white;
    border-radius: 10px;
    box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1);
    padding: 20px;
    margin-bottom: 20px;
    max-width: 600px;
    margin-left: auto;
    margin-right: auto;
    transition: transform 0.3s ease, box-shadow 0.3s ease;
}

.comment-card:hover {
    transform: translateY(-5px);
    box-shadow: 0 8px 12px rgba(0, 0, 0, 0.2);
}

.comment-text {
    font-size: 1rem;
    color: #555;
    margin-bottom: 10px;
}

.comment-date {
    font-size: 0.9rem;
    color: #888;
    margin-bottom: 10px;
}

.admin-response {
    margin-top: 10px;
    font-size: 0.9rem;
    color: #007bff;
}

.comment-actions {
    margin-top: 10px;
    display: flex;
    gap: 10px;
    justify-content: center;
}

.comment-actions button {
    padding: 5px 10px;
    border: none;
    border-radius: 5px;
    cursor: pointer;
    font-size: 0.9rem;
}

.comment-actions button:first-child {
    background-color: #28a745;
    color: white;
}

.comment-actions button:last-child {
    background-color: #dc3545;
    color: white;
}

/* Style du nom de l'utilisateur */
.comment-name {
    font-size: 1.1rem;
    color: #333;
    margin-bottom: 5px;
}

/* Bouton "Voir plus" */
#comments-list button {
    margin-top: 10px;
    padding: 10px 20px;
    border: none;
    border-radius: 5px;
    cursor: pointer;
    font-size: 1rem;
}

#comments-list button:first-of-type {
    background-color: #007bff;
    color: white;
}

#comments-list button:last-of-type {
    background-color: #dc3545;
    color: white;
}